.breadcrumbs {
    display: flex;
    flex-wrap: wrap;
    padding: 45px 0 15px;
    line-height: 1.2;
    a {
        display: flex;
        align-items: flex-start;
        transition: all .2s;
        &:hover {
            color: var(--Grey);
        }
    }
    a::after {
        content: "/";
        margin: 0 7px;
        display: inline-block;
    }
    span {
        color: var(--currentPage);
    }
}